<!-- 本页面属于 维修单详情的 新建状态 status === 10 -->
<template>
  <view class="task-detail padding-bottom-normal">
    <u-navbar
        isFixed
        :borderBottom="false"
        backIconName="arrow-leftward"
        titleSize="34"
        :customBack="back"
        :background="background"
        title="维修单详情"></u-navbar>
    <view class="margin-normal bg-white padding-normal radius-normal" @click="foMaintProcess">
      <u-steps mode="number" :active-color="'#24A27C'" :list="numList" :current="current"></u-steps>
    </view>

    <view v-if="detail && !empty">
      <view>
        <nnkjc-detail-group>
          <nnkjc-detail-item
              detailTitle="工单编号"
              :detailContent="detail.seqNo"
              :align="'left'"
          >
          </nnkjc-detail-item>

          <nnkjc-detail-item
              detailTitle="创建时间"
              :detailContent="detail.createTime"
              :align="'left'"
          >
          </nnkjc-detail-item>

          <nnkjc-detail-item
              detailTitle="最近更新"
              :detailContent="detail.updateTime"
              :align="'left'"
          >
          </nnkjc-detail-item>

          <nnkjc-detail-item
              detailTitle="当前状态"
              :detailContent="detail.statusText"
              :align="'left'"
              :gap="false"
          >
          </nnkjc-detail-item>

        </nnkjc-detail-group>

        <nnkjc-detail-group>
          <nnkjc-detail-item
              detailTitle="故障原因"
              :detailContent="detail.content"
              :align="'left'"
              :gap="false"
          >
          </nnkjc-detail-item>
        </nnkjc-detail-group>
      </view>

      <!--      修改故障原因 取消工单-->
      <view>
        <view v-show="showBtn1" class="save-tasks-inner-wrapper margin-normal radius-normal">
          <u-button ripple type="success" :loading="loading" @click="modifyReason">修改故障原因</u-button>
        </view>

        <view v-show="showBtn2" class="save-tasks-inner-wrapper margin-normal radius-normal">
          <u-button ripple :loading="loading" type="error" plain @click="showModal = true">取消工单</u-button>
        </view>
      </view>

      <view>
        <view v-show="showBtn3" class="save-tasks-inner-wrapper margin-normal radius-normal">
          <u-button ripple type="success" :loading="loading" @click="$u.debounce(delegatePerson, 500)">委派维修人</u-button>
        </view>
      </view>

    </view>

    <u-empty :show="empty" text="暂无任务详情" mode="history"></u-empty>

    <u-modal v-model="showModal" @confirm="$u.debounce(confirmCalcel, 500)" show-cancel-button
             content="确认取消工单？"></u-modal>

    <u-select
        title="选择维修人"
        :default-value="personDefaultV"
        mode="single-column"
        v-model="showPerson"
        @confirm="personConfirm"
        @cancel="personCancel"
        :list="personList"></u-select>

    <u-toast ref="uToast"/>
  </view>
</template>

<script>
  export default {
    name: "task-detail",
    data() {
      return {
        personIdIndexMap: {},
        showPerson: false,
        personDefaultV: [0],
        personList: [],
        showBtn1: false,
        showBtn2: false,
        showBtn3: false,

        showModal: false,
        loading: false,
        empty: false,
        detail: {
          seqNo: "",
          createTime: "",
          updateTime: "",
          statusText: ""
        },
        background: {
          'background': '#f2f2f8'
        },
        current: 0,
        numList: [{
          name: '新建'
        }, {
          name: '委派'
        }, {
          name: '维修'
        }, {
          name: '检定'
        },],
        searchParams: {
          id: "",
          type: "",
          status: ""
        }
      }
    },
    computed: {
      PREMISS() {
        return this.$store.state.PREMISS
      }
    },
    onLoad(options) {
      this.searchParams.id = options.taskId
      this.searchParams.status = options.status
      this.searchParams.type = options.type
    },
    onReady() {
      this.getDetail();
    },
    onShow() {
      this.getPersons();
    },
    methods: {
      getPersons() {
        this.$u
            .post("/sys/staff/getStaffListForWexiu")
            .then(res => {
              if (res.data) {
                this.personList = res.data.map((person, index) => {
                  this.personIdIndexMap[person.id] = index;
                  return {
                    value: person.id,
                    label: person.staffName
                  }
                })
              }
            })
            .catch(err => {
            });
      },
      personConfirm(v) {
        this.personDefaultV = [this.personIdIndexMap[v[0].value] || 0];
        this.loading = true;
        this.$u.post("maint/saveSend", {
          taskId: this.searchParams.id,
          maintStaffIds: v[0].value,
          type: this.searchParams.type
        }).then(res => {
          if (res.code === 200) {
            this.$refs.uToast.show({
              title: "委派成功",
              type: "success"
            });
            setTimeout(() => {
              uni.reLaunch({
                url: "/pages/home/index"
              })
            }, 500)

          }
        }).catch(e => {

        }).finally(_ => {
          this.showPerson = false;
          this.loading = false;
        })
      },
      personCancel() {
        this.showPerson = false;
      },
      back() {
        uni.navigateBack({
          delta: 1
        })
      },
      modifyReason() {
        const params = this.$u.queryParams({
          taskId: this.detail.id,
          status: this.searchParams.status,
          type: this.searchParams.type
        });
        uni.navigateTo({
          url: "/pages/home/modify-task/modify-task" + params
        })
      },
      confirmCalcel() {
        this.$u
            .post("/maint/delTask", {
              id: this.detail.id
            })
            .then(res => {
              if (res.code === 200) {
                this.$store.commit("freshTaskFunc", true);
                uni.reLaunch({
                  url: "/pages/home/index"
                })
              }
            })
            .catch(err => {
            });
      },
      foMaintProcess() {
        const params = this.$u.queryParams({
          taskId: this.searchParams.id,
        })

        uni.navigateTo({
          url: "/pages/maint-process/index" + params
        })
      },
      getDetail() {
        this.$u
            .post("/maint/getWeixiuTaskDetail", this.searchParams)
            .then(res => {
              if (res.data) {
                const user = uni.getStorageSync("user")

                const premissObj = uni.getStorageSync("premissObj")
                this.detail = res.data;
                if (this.detail) {
                  //  本人 id 是否是建单人
                  const rightDetail = user && user.staffId === this.detail.createStaffId;
                  const rightStatus = this.searchParams.status == "10"

                  //  状态是 10 && 有修改的权限 && staffID 一致 (见详情)
                  this.showBtn1 = !!(rightStatus && premissObj && premissObj[this.PREMISS.modifyreason] && rightDetail)

                  //  状态是 10 && 有取消的权限 && staffID 一致 (见详情)
                  this.showBtn2 = !!(rightStatus && premissObj && premissObj[this.PREMISS.delitem] && rightDetail)

                  //  状态是 10 && 有委派的权限
                  this.showBtn3 = !!(rightStatus && premissObj && premissObj[this.PREMISS.delegate])

                  const res = [{
                    "表单状态": this.detail.status,
                    "表单 mstaffId": this.detail.maintStaffId,
                    "用户 staffId": user.staffId,
                    "表单类型": this.detail.type,
                    "修改权限": premissObj[this.PREMISS.modifyreason],
                    "取消权限": premissObj[this.PREMISS.delitem],
                    "委派权限": premissObj[this.PREMISS.delegate],
                    "重新委派权限": premissObj[this.PREMISS.reDelegate],
                    "接单权限": premissObj[this.PREMISS.reveive],
                    "拒单权限": premissObj[this.PREMISS.refuse],
                  }];

                  console.table(res);
                }
              } else {
                this.empty = true;
              }
            })
            .catch(err => {
            });
      },
      delegatePerson() {
        this.showPerson = true;
      }
    }
  }
</script>

<style scoped>
  .u-btn--error--plain {
    background-color: #f2f2f8 !important;
  }
</style>